html { font-size: 30px; }

* {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  margin: 0;
  padding: 0;
}

html, body, .wrapper, #root { 
  height: 100%;
  width: 100%;
  position: relative;
  overflow: auto;
}

body {
  background-image: radial-gradient(circle at center, rgba(0,0,0,0.3) 0, rgba(0,0,0,0.3) 30%, white 11%, white 100%);
  background-size: 0.8vh 0.8vh;
}

.wrapper {
  display: grid;
  grid-template-rows: repeat(10,1fr);
  grid-template-columns: repeat(10,1fr);
}

.display {
  background-color: white;
  box-shadow: inset 0 0 10px -2px rgba(0,0,0,0.5);
  font-family: monospace;
  text-align: center;
  margin: 0.5rem;
  font-size: 2.4rem;
  padding: 1rem;
  grid-column-start: 2;
  grid-column-end: 10;
  grid-row-start: 1;
  grid-row-end: 3;
  display: flex;
  justify-content: center;
  align-items: center;
}

.keyboard {
  grid-row-start: 3;
  grid-row-end: 10;
  grid-column-start: 1;
  grid-column-end: 11;
  padding: 1rem;
}

.keyboard-wrapper{
  display: grid;
  grid-template-rows: repeat(5,1fr);
  grid-template-columns: repeat(10,1fr);
  gap: 3vw;
  width: 100%;
  height: 100%;
}

.key:nth-child(-n+50) { background-color: rgb(173, 168, 247); }
.key:nth-child(-n+40) { background-color: rgb(244, 168, 247); }
.key:nth-child(-n+30) { background-color: rgb(255, 174, 187); }
.key:nth-child(-n+20) { background-color: rgb(247, 197, 168); }
.key:nth-child(-n+10) { background-color: rgb(247, 247, 168); }

.key {
  font-size: 1.5rem;
  width: 100%;
  height: 100%;
  margin: 0.3vw;
  font-family: sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 0.1rem solid black;
  text-transform: uppercase;
  border-radius: 5%;
  cursor: pointer;
  user-select: none;
  &:hover {
    filter: saturate(200%);
    box-shadow: 0 0 2px 0px rgba(0,0,0.5);
  }
  &:active {
    filter: saturate(200%);
    box-shadow: inset 0 0 2px 0px rgba(0,0,0.5);
  }
}

.key:last-child {
  grid-column-start: 1;
  grid-column-end: 11;
  border-radius: 0;
}

.delete.key {
  position: absolute;
  bottom: 0;
  width: 100%;
  margin: 0;
  padding: 1rem;
  height: 6vh;
  background-color: rgb(214, 90, 90);
  border-radius: 0;
}